import React from 'react';
import PropTypes from 'prop-types';

import './row.css';

const Row = ({left, right}) => {
  return (
    <section className="row mb2">
    <div className="col-md-6">
      {left}
    </div>
    <div className="col-md-6">
      {right}
    </div>
  </section>
  );
};

Row.propTypes = {
  left: PropTypes.node,
  right: PropTypes.node
}

export default Row;